import React from 'react';
import './Landing.css';
import { Link } from "react-router-dom";
import VideoFutbol from "./Video/VideoFutbol1.mp4";
import { Footer } from '../../Components/Footer/Footer';
import SportsSoccerIcon from '@mui/icons-material/SportsSoccer';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import AssessmentIcon from '@mui/icons-material/Assessment';
import GroupIcon from '@mui/icons-material/Group';
import EventNoteIcon from '@mui/icons-material/EventNote';
import PlaylistAddCheckIcon from '@mui/icons-material/PlaylistAddCheck';
import PeopleIcon from '@mui/icons-material/People';
import SettingsIcon from '@mui/icons-material/Settings';

export function Landing() {
  const scrollToSection = () => {
    const section = document.querySelector('.third-section');
    section.scrollIntoView({ behavior: 'smooth' });
  };
  return (
    <div className="landing">

{/* SECCIN UNO */}
      <section className="first-section">
        <div className="video-overlay"></div> {/* Velo negro casi transparente */}
        <video src={VideoFutbol} autoPlay loop muted></video>
        <div className="column">
        <h1 className="scouters">
              Scouters <span className='NewEra'>NEW ERA</span>
            </h1>
        <div className="green-rectangle"></div>
        <div className='parrafo'>     
          <p>Bienvenidos a <span className='green-text'>Scouters New Era</span>, la herramienta revolucionaria para scouts de ftbol. <br /> <br />
            Genera informes en tiempo real de los jugadores mientras los observas en accin y obtn una media precisa para comparar nuevos talentos ojeados.<br /> <br />
            Potencia tu capacidad de evaluacin con datos objetivos y anlisis detallados. nete ahora y lleva tu scouting al siguiente nivel!</p>
        </div> 

        <div className='botones'>
          <Link to="/registro"><button>REGSTRATE</button></Link>
          <Link to="/login"><button>INICIA SESIN</button></Link>         
        </div>
        
        </div>
        <div className="column">
          <img className='balon' src="/assets/fotos/balon.png" alt="Imagen de la primera seccin" />      
        </div>
      </section>



    {/* SECCIN DOS */}
      <section className="second-section">
        <div className="column1-section2">
          <h2>Crea, Gestiona y Compara Informes de Jugadores</h2>
          <br />
          <p>Potencia tu scouting con informes de jugadores personalizados. <br /> 
          Toma decisiones acertadas y descubre nuevos talentos en el mundo del ftbol.</p>
          <button onClick={scrollToSection}>Leer Ms</button>
        </div>
        <div className="column2-section2">       
          <img src="/assets/fotos/futbol3.jpg" alt="Imagen de la segunda seccin" />
        </div>
      </section>



      {/* SECCIN TRES */}
      <section className="third-section">
        <div className='service-header'>
          <h3>UNA HERRAMIENTA, <span class="highlight">MUCHAS SOLUCIONES</span></h3>
          <p>Descubre cmo nuestra herramienta revoluciona el mundo del scouting de ftbol</p>
        </div>
  <div className='service-container'>
    <div className="service-row">
      <div className="service">
        <SportsSoccerIcon />
      <p>Sistema de seguimiento de jugadores</p>
    </div>
    <div className="service">
      <CompareArrowsIcon />
      <p>Comparacin de estadsticas y rendimiento</p>
    </div>
    <div className="service">
      <AssessmentIcon />
      <p>Mtricas avanzadas de jugadores</p>
    </div>
    <div className="service">
      <GroupIcon />
      <p>Gestin de equipos y plantillas</p>
    </div>
  </div>
  <div className="service-row">
    <div className="service">
      <EventNoteIcon />
      <p>Calendario de partidos y eventos</p>
    </div>
    <div className="service">
      <PlaylistAddCheckIcon />
      <p>Seguimiento de progresin de jugadores</p>
    </div>
    <div className="service">
      <PeopleIcon />
      <p>Anlisis de rendimiento colectivo</p>
    </div>
    <div className="service">
      <SettingsIcon/>
      <p>Personalizacin y configuracin avanzada</p>
    </div>
  </div>
  </div>
</section>
    <Footer/>
    </div>
  );
}



